import React from 'react';
import { Table } from "antd";
import 'antd/dist/antd.css';
import data from "./data.json"
import { StarFilled } from '@ant-design/icons';
import moment from "moment"
const formatDate = (date)=>{
  return moment(date).format('YYYY-MM-DD')
}
function List(props) {
  return <div>
    <Table
      columns={[
        {
          title: '',
          dataIndex: 'projectStatus',
          render: () => <div className='star'><StarFilled /></div>
        },
        {
          title: '',
          dataIndex: 'productCategory',
        },
        {
          title: '',
          dataIndex: 'prjCategory',
        },
        {
          title: '',
          dataIndex: '',
        },
        {
          title: '',
          dataIndex: 'prjName',
        },
        {
          title: '',
          dataIndex: 'projectStatus',
          render:(_,record)=>{
            return <div className='status'><span className={`${record.projectStatus} point`} /> {record.projectStatus === 'approving' ? '' : ''}</div>
          }
        },
        {
          title: '',
          dataIndex: 'prjManager',
        },
        {
          title: '',
          dataIndex: 'department',
        },
        {
          title: '',
          dataIndex: 'department',
          render:(_,record)=><span>{formatDate(record.prjStartDate)}~{formatDate(record.prjEndDate)}</span>
        },
      ]}
      dataSource={data}
      pagination={false}
    />
  </div>
}

export default List