import React from 'react';
import data from "./data.json"
import headImg from './image.jpg';
import { StarFilled } from '@ant-design/icons';
import moment from "moment"
const formatDate = (date)=>{
  return moment(date).format('YYYY-MM-DD')
}
function Card() {
  return <div className='card'>
    {data.map((item) => <div className='item'>
      <div className='head'><img src={headImg} /></div>
      <div className='info'>
        <div className='prjName'>{item.prjName}</div>
        <div className='prjManager'>{item.prjManager}</div>
        <div className='prjStartDate'>{formatDate(item.prjStartDate)}</div>
        <div>{item.taskCount} {item.taskDoneCount} {item.taskDoingCount}</div>
      </div>
      <div className='option'>
          <div className='status'><span className={`${item.projectStatus} point`} /> {item.projectStatus === 'approving' ? '' : ''}</div>
          <div className='star'><StarFilled /></div>
      </div>
    </div>)}
  </div>
}

export default Card